<template>
 <div class="content mei_list">
	 <diyT></diyT>
  	<div class="card-list">
  		<div class="con-list">
  	 		<ul class="clearfix">
  	 			<li class="first">
  	 				<div class="photo-card" style=" background:#f0f0f0;">
  	 					<a href="mei_edit.html" class="upload_btn">
  	 						<i class="iconfont">&#xe602;</i>
  	 						<p>上传图片/文字</p>
  	 					</a>
  	 				</div>
  	 			</li>
  	 			<li v-for="(item,index) of Mdata" :key="index">
					<router-link :to="path+'/detail?id='+index+'&deName='+name">
						<div class="photo-card">
							<a href="#" class="img" style="background-image: url(../../static/images/img5.jpg);"><img :src="item.img"/></a>
							<p class="txt"><a href="#">{{item.title}}</a></p>
							<div class="info clearfix">
								<span class="author"><img :src="item.auimg">{{item.author}}</span>
								<span class="zan"><i class="iconfont">&#xe600;</i>{{item.love}}</span>
								<span class="collect"><i class="iconfont">&#xe605;</i>{{item.star}}</span>
							</div>
						</div>
					</router-link>
  	 			</li>
  	 		</ul>
  	 	</div>
  	</div>
  </div>
 
</template>

<script>
import diyT from './diy-h'
export default {
	components:{
		diyT
	},
	data(){
		return {
		// count:0  this.$store.dispatch(type,payload)
		Mdata:[],
		path:'',
		name:''
		}
 	 },
 	 mounted(){
		let name = this.$route.query.name;
		this.name = name;
		let path =  this.$route.path;
		this.path= path;
		// console.log(name)
		this.Mdata =this.$store.state.dataIndex[name];
		// console.log(this.Mdata)
		
 	 }
}
</script>

<style>
	.mei_list .card-list .con-list .photo-card{ background: #fff;}
	.mei_list .card-list .con-list ul{ padding-right: 6px;}
	.mei_list .card-list .con-list li { margin-bottom: 10px;}
	.mei_list .card-list .con-list li .photo-card{ margin-left: 6px;border:5px solid #fff;}
	.mei_list .card-list .con-list li.first .upload_btn{display:block; text-align: center; padding: 68px 0 30px 0; background: #f0f0f0;}
	.mei_list .card-list .upload_btn i{ color: #cecece; font-size: 3rem;}
	.mei_list .card-list .upload_btn p{ color: #cecece; margin-top: 10px;}
</style>



